/* animation header */
@keyframes moveInLeft {
    0% {
        opacity: 0;
        transform: translateX(-10rem);
    }

    80% {
        transform: translateX(1rem);

    }

    100% {
        opacity: 1;
        transform: translate(0);

    }
}

@keyframes moveInRight {
    0% {
        opacity: 0;
        transform: translateX(10rem);
    }

    80% {
        transform: translateX(-1rem);

    }

    100% {
        opacity: 1;
        transform: translate(0);

    }
}
@keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(3rem);
    }
    100% {
        opacity: 1;
        transform: translate(0);

    }
}

@keyframes moveInTop {
    0% {
        opacity: 0;
        transform: translateY(-3rem);
    }
    100% {
        opacity: 1;
        transform: translate(0);

    }
}

@keyframes scale {
    0% {
        opacity: 0;
        transform: scale(.8);
    }

    80% {
        transform: scale(1.1);

    }

    100% {
        opacity: 1;
        transform: scale(1);

    }
}

/* animation portfolio */

@keyframes portfolio {
    0% {
        opacity: 0;
        transform: translateY(3rem);
    }
    100% {
        opacity: 1;
        transform: translate(0);

    }
}

/* modal animation */
@keyframes modalIn {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }
    70%{
        opacity: .5;  
    }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }

}

@keyframes modalOut {
    0% {
        opacity: 0;
        transform: translateY(0%);
    }
    70%{
        opacity: .5;  
    }
  100% {
    opacity: 1;
    transform: translateY(100%);
  }

}

@keyframes opacityModal{
    0% {
        opacity: 0;
    }
    70%{
        opacity: .5;  
    }
  100% {
       opacity: 1;
  }

}

